import React from "react";
import logo from "../images/images/logo_02.png";
import $ from 'jquery';
import { NavLink } from "react-router-dom";

const Header = () => {
  $('.menu').click(function(e){
    e.preventDefault();
    var target = $($(this).attr('href'));
    if(target.length){
      var scrollTo = target.offset().top;
      $('body, html').animate({scrollTop: scrollTo+'px'}, 800);
    }
  });
return<>
   <header id="header_section">
  <div className="container">
    <div className="header_wrapper">
      <NavLink exact to="/" className="logo">
        <img src={logo} alt="logo-icon"/>
      </NavLink>
      <div className="navigation_bar">
            <nav className="navbar navbar-expand-lg">
              <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                  <a className="menu-bar" href="#">
                    <span className="bars bar1"></span>
                    <span className="bars bar2"></span>
                    <span className="bars bar3"></span>
                  </a>
                </button>
                <div className="collapse navbar-collapse" id="collapsibleNavbar">
              <ul>
                <li><a href="#about_me" className="menu">About</a></li>
                <li><NavLink exact to="/Resources">Resources</NavLink></li>
                <li><a href="#services" className="menu">services</a></li>
                <li><NavLink exact  to="/FreeSeo">free seo</NavLink></li>
                <li><NavLink exact  to="/Guide">guide</NavLink></li>
                <li><a href="#blog" className="menu">blog</a></li>
                <li><NavLink exact  to="/Contact">contact</NavLink></li>
                <li className="lets_btn"><a href="#">lets talks!</a></li>
                <span className="count">0</span>
              </ul>
          </div>
          </nav>
          </div>
        
    </div>
  </div>
</header>
   </>   
}
export default Header;